import React, { useState } from 'react';
import styled, { keyframes } from 'styled-components';
import emailjs from 'emailjs-com';


import hi from 'D:\\NReact\\myportfolio\\src\\images\\image.gif';


// Animation
const fadeIn = keyframes`
  from { opacity: 0; transform: translateY(30px); }
  to { opacity: 1; transform: translateY(0); }
`;

const FormContainer = styled.div`
  
  background: #black;
 
  color: white;
  text-align: center;
  animation: ${fadeIn} 1s ease-out;
`;

const Title = styled.h2`
  font-size: 2rem;
  color: #f39c12;
  margin-bottom: 30px;
`;

const Input = styled.input`
  margin: 10px;
  padding: 12px;
  width: 300px;
  background: #222;
  color: white;
  border: 1px solid #444;
  border-radius: 5px;
`;

const Textarea = styled.textarea`
  margin: 10px;
  padding: 12px;
  width: 300px;
  height: 120px;
  background: #222;
  color: white;
  border: 1px solid #444;
  border-radius: 5px;
`;

const SubmitButton = styled.button`
  padding: 12px 25px;
  background-color: #3498db;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  margin-top: 10px;
  transition: background 0.3s;

  &:hover {
    background-color: #2980b9;
  }
`;

const SuccessMessage = styled.p`
  color: #2ecc71;
  margin-top: 15px;
`;



const Footer = styled.footer`
  background-color: #2d3436;
  color: white;
  padding: 20px;
  text-align: center;
  position: relative;
  bottom: 0;
  width: 100%;
`;

const FooterLink = styled.a`
  color: white;

  text-decoration: none;
  margin: 0 10px;
  transition: color 0.3s ease;
  &:hover {
    color: #f39c12;
  }
`;




const ContactForm = () => {
  const [form, setForm] = useState({ name: '', email: '', message: '' });
  const [sent, setSent] = useState(false);

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    emailjs.send(
      'service_rn77v5k',      // Replace with your EmailJS service ID
      'template_1cifrys',     // Replace with your EmailJS template ID
      form,
      '2QkT7I_rnDhBjJCNl'       // Replace with your EmailJS public key
    )
    .then(() => {
      setSent(true);
      setForm({ name: '', email: '', message: '' });
    })
    .catch((err) => console.log(err));
  };

  return (
    <FormContainer>
      <Title>Contact Me</Title>
      <form onSubmit={handleSubmit}>
        <Input type="text" name="name" placeholder="Your Name" value={form.name} onChange={handleChange} required />
        <br />
        <Input type="email" name="email" placeholder="Your Email" value={form.email} onChange={handleChange} required />
        <br />
        <Textarea name="message" placeholder="Your Message" value={form.message} onChange={handleChange} required />
        <br />
        <SubmitButton type="submit">Send Message</SubmitButton>
      </form>
      {sent && <SuccessMessage>Message sent successfully!</SuccessMessage>}
      <br/>

      <img src={hi} alt="Nandita Suryawanshi" className="profile-image" />






      {/* Footer Section */}
      <Footer>
        <p>Connect with me:</p>
        <FooterLink href="https://linkedin.com/in/your-linkedin" target="_blank">
          LinkedIn
        </FooterLink>
        <FooterLink href="https://github.com/your-github" target="_blank">
          GitHub
        </FooterLink>
        <FooterLink href="mailto:nandita212002@gmail.com">Email</FooterLink>
        <p>&copy; 2025 Nandita Vivek Suryawanshi. All rights reserved.</p>
      </Footer>


    </FormContainer>
  );
};

export default ContactForm;
